body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media only screen and (max-width: 650px) {
    p,li {
        font-size: 0.6em!important;
    }

    h1 {
        font-size: 1.0em!important;
    }

    h2 {
        font-size: 0.8em!important;
    }

    h3 {
        font-size: 0.8em!important;
    }

    a {
        font-size: 0.8em!important;
    }
}

@media only screen and (max-width: 550px) {
    a {
        font-size: 0.7em!important;
    }
}

.Header {
    background-image: url(/static/media/water2.5a9c6134.jpg);
    background-size: cover;
    flex-basis: 400px;
    min-height: 400px;
}

/* The animation code */
@-webkit-keyframes slide-first {
    from {
        -webkit-transform: translateX(-200px);
                transform: translateX(-200px);
    }
    to {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
        }
  }
@keyframes slide-first {
    from {
        -webkit-transform: translateX(-200px);
                transform: translateX(-200px);
    }
    to {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
        }
  }

  @-webkit-keyframes slide-last {
    from {
        -webkit-transform: translateX(-230px);
                transform: translateX(-230px);
    }
    to {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
        }
  }

  @keyframes slide-last {
    from {
        -webkit-transform: translateX(-230px);
                transform: translateX(-230px);
    }
    to {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
        }
  }

  @-webkit-keyframes slide-passions-right {
    from {
        -webkit-transform: translateX(400px);
                transform: translateX(400px);
    }
    to {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
        }
  }

  @keyframes slide-passions-right {
    from {
        -webkit-transform: translateX(400px);
                transform: translateX(400px);
    }
    to {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
        }
  }

  @-webkit-keyframes slide-passions-left {
    from {
        -webkit-transform: translateX(-400px);
                transform: translateX(-400px);
    }
    to {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
        }
  }

  @keyframes slide-passions-left {
    from {
        -webkit-transform: translateX(-400px);
                transform: translateX(-400px);
    }
    to {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
        }
  }

.firstName {
    position: absolute;
    color: white;  
    font-size: 1.6em;
    font-weight: 700;
    padding: 10px 40px 10px 40px;
    line-height: 40px;
    top: 50px;
    background-color: black;
    -webkit-animation-name: slide-first;
            animation-name: slide-first;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

.lastName {
    position: absolute;
    color: white;
    font-size: 1.6em;
    font-weight: 700;
    padding: 10px 50px 10px 40px;
    line-height: 40px;
    top: 115px;
    background-color: black;
    -webkit-animation-name: slide-last;
            animation-name: slide-last;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

.passions {
    position: absolute;
    right: 0;
    top: 250px;
    padding: 10px 40px 10px 40px;
    color: white;
    font-weight: 700;
    font-size: 2.4em;
    background-color: black;
    -webkit-animation-name: slide-passions-right;
            animation-name: slide-passions-right;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    max-width: 70%;
}

@media only screen and (max-width: 1200px) {

   .firstName, .lastName {
       font-size: 1.4em!important;
   } 

   .passions {
       font-size: 2.0em!important;
   }
}

@media only screen and (max-width: 1000px){
    .passions {
        left: 0!important;
        right: auto;
        -webkit-animation-name: slide-passions-left;
                animation-name: slide-passions-left;
    }
}

@media only screen and (max-width: 800px){
    .passions {
        display: none;
    }
}

@media only screen and (max-width: 600px){
    .Header {
        min-height: 250px!important;
    }
}
.Navbar {
    min-height: 50px;
    display: flex;
    flex-basis: 100%;
    padding: 0px 40px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: black;
    color: white;
}

@-webkit-keyframes slide-nav {
    from {
        -webkit-transform: translate(-200px, 0px);
                transform: translate(-200px, 0px);
    }
    to {
        -webkit-transform: translate(0px, 0px);
                transform: translate(0px, 0px);
        }
  }

@keyframes slide-nav {
    from {
        -webkit-transform: translate(-200px, 0px);
                transform: translate(-200px, 0px);
    }
    to {
        -webkit-transform: translate(0px, 0px);
                transform: translate(0px, 0px);
        }
  }

.navItem {
    -webkit-animation-name: slide-nav;
            animation-name: slide-nav;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    text-align: center;
    flex-basis: auto;
    font-size: 1.3em;
    font-weight: 600;
}

.navItem:hover {
    color: grey;
    cursor: pointer;
}

@media only screen and (max-width: 600px){
    .Navbar {
        padding: 0px 10px;
    }
}

.Me {
    /* top colors:
   #AEE9EC
   #46D2D1 
   #41C2C1
   #9FD4DC
   */
    background-color: #98D0D7;
    min-height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 50px 5%;
    font-size: 1.4em;
}

.hello {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

img {
    height: 200px!important;
    margin-left: 40px;
}

.hello > .greeting {
    flex-basis: 400px;
    max-width: 1100px;
    flex-grow: 1;
    margin: 0px 40px;
}

.info {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 30px;
    width: 100%;
    
}

.info > * {
    margin: 0px 40px;
}

.skills {
    flex-basis: 500px;
    flex-grow: 1;
}

.facts {
    flex-basis: 400px;
    flex-grow: 1;
}

.facts > ul {
    max-width: 700px;
}

.skillsContent {
    display: flex;
    justify-content: space-between;
    max-width: 800px;
}

@media only screen and (max-width: 1000px) {
    .skillsContent{
        flex-wrap: wrap;
    }
}

.leftList {
    margin-right: 40px;
}

.lists {
    display: flex;
}

.lists > * {
    margin-right: 20px;
}

span {
    font-weight: 700;
}

h1 {
    margin: 10px 0px;
    font-size: 1.8em;
}

h2 {
    margin: 10px 0px;
    border-bottom: solid 2px black;
}

h3 {
    margin: 10px 0px 0px 0px;
}

p {
    margin: 10px 0px;
    font-size: 1.0em;
    font-weight: 500;
}

ul {
    margin: 20px 0px;
}

/* Mobile Friendly */
@media only screen and (max-width: 600px) {

    .Me {
        padding: 30px 0px !important;
    }

    img {
        margin-left: 30px!important;
    }

    .hello > .greeting {
        margin: 0px 30px !important;
    }

    .info > * {
        margin: 0px 30px!important;
    }

    .leftList {
        margin-right: 0px!important;
    }

    .lists > * {
        margin-right: 10px!important;
    }
}
.Work {
    background-color: black;
    min-height: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Work > p {
    font-size: 3.0em;
    text-align: center;
    color: white;
}

